html {
  box-sizing: border-box;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: $museo;
  font-size: 17px;
  line-height: 28px;
  font-weight: 400;
  font-style: normal;
  color: $text-color;
  background-color: $white;
  display: flex;
  flex-direction: column;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

@media(max-width:992px) {
  .js-close {
    transform-origin: 0 0;
    transform: scaleY(0);
    transition: all .2s ease-in;
  
    opacity: 0;
  }  
}

